import {
  PAYMENT_SHIPPING_METHODS_GET,
  PAYMENT_GATEWAYS_GET,
  PAYMENT_ERROR
} from "../actions/actionTypes";

const INITIAL_STATE = {
  shippingMethods: [],
  gateways: [],
  error: false
};

export default function paymenteReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case PAYMENT_SHIPPING_METHODS_GET:
      return {
        ...state,
        shippingMethods: action.dados
      };
    case PAYMENT_GATEWAYS_GET:
      return {
        ...state,
        gateways: action.dados
      };
    case PAYMENT_ERROR:
      return {
        ...state,
        shippingMethods: [],
        gateways: [],
        error: action.error
      };
    default:
      return state;
  }
}
