import React from "react";
import { CircularProgress, Box } from "@material-ui/core";

const MainLoader = () => {
  return (
    <Box
      display="flex"
      p={5}
      bgcolor="background.paper"
      justifyContent="center"
      alignItems="center"
      style={{ width: "100%", height: "auto" }}
    >
      <CircularProgress />
    </Box>
  );
};

export default MainLoader;
