import {
  CUSTOMER_LOGIN,
  CUSTOMER_LOGOUT,
  CUSTOMER_STORE,
  CUSTOMER_ERROR
} from "../actions/actionTypes";

const INITIAL_STATE = {
  id: 0,
  data: [],
  error: false
};

export default function customerReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case CUSTOMER_LOGIN:
      return {
        ...state,
        id: action.dados.id,
        data: action.dados
      };
    case CUSTOMER_LOGOUT:
      return {
        ...state,
        id: 0,
        data: []
      };
    case CUSTOMER_STORE:
      return {
        ...state,
        id: 0,
        data: []
      };
    case CUSTOMER_ERROR:
      return {
        ...state,
        id: 0,
        data: [],
        error: action.error
      };
    default:
      return state;
  }
}
