import {
  PRODUTOS_GROUPED_GET,
  PRODUTOS_SIMPLE_GET,
  PRODUTOS_ERROR
} from "../actions/actionTypes";

const INITIAL_STATE = {
  grouped_products: [],
  simple_products: [],
  error: false
};

export default function productsReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case PRODUTOS_GROUPED_GET:
      return {
        ...state,
        grouped_products: action.products,
        error: false
      };
    case PRODUTOS_SIMPLE_GET:
      return {
        ...state,
        simple_products: action.products,
        error: false
      };
    case PRODUTOS_ERROR:
      return {
        ...state,
        grouped_products: [],
        simple_products: [],
        error: action.error
      };
    default:
      return state;
  }
}
