import React, { lazy } from "react";
import { Route } from "react-router-dom";

const Home = lazy(() => import("./pages/home"));
const CompreAqui = lazy(() => import("./pages/compreaqui"));
const Camarotes = lazy(() => import("./pages/camarotes"));
const Blocos = lazy(() => import("./pages/blocos"));
const CentralDoCliente = lazy(() => import("./pages/centraldocliente"));
const Camarote = lazy(() => import("./pages/camarote"));
const Bloco = lazy(() => import("./pages/bloco"));

export default (
  <React.Fragment>
    <Route exact path="/" component={Home} />
    <Route path="/compre-aqui" component={CompreAqui} />
    <Route path="/camarotes" component={Camarotes} />
    <Route path="/blocos" component={Blocos} />
    <Route path="/minha-conta" component={CentralDoCliente} />
    <Route path="/camarote" component={Camarote} />
    <Route path="/bloco" component={Bloco} />
  </React.Fragment>
);
