import React from "react";
import { Link as RouterLink } from "react-router-dom";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";

import Divider from "@material-ui/core/Divider";

import HelpOutlineOutlinedIcon from "@material-ui/icons/HelpOutlineOutlined";
import HeadsetIcon from "@material-ui/icons/Headset";
import NearMeIcon from "@material-ui/icons/NearMe";
import WhatsAppIcon from "@material-ui/icons/WhatsApp";
import QuestionAnswerOutlinedIcon from "@material-ui/icons/QuestionAnswerOutlined";

export default () => {
  return (
    <div>
      <List>
        <ListItem button component={RouterLink} to="/compre-aqui">
          <ListItemText primary="Compre aqui" />
        </ListItem>
        <ListItem button component={RouterLink} to="/camarotes">
          <ListItemText primary="Camarotes" />
        </ListItem>
        <ListItem button component={RouterLink} to="/blocos">
          <ListItemText primary="Blocos" />
        </ListItem>
        <ListItem button component={RouterLink} to="/navios">
          <ListItemText primary="Navios" />
        </ListItem>
        <ListItem button component={RouterLink} to="/hoteis">
          <ListItemText primary="Hotéis" />
        </ListItem>
      </List>
      <Divider />
      <List>
        <ListItem button component={RouterLink} to="/quem-somos">
          <ListItemIcon>
            <NearMeIcon />
          </ListItemIcon>
          <ListItemText primary="Quem somos" />
        </ListItem>
        <ListItem button component={RouterLink} to="/entrega">
          <ListItemIcon>
            <NearMeIcon />
          </ListItemIcon>
          <ListItemText primary="Entrega de abadá" />
        </ListItem>
        <ListItem button component={RouterLink} to="/passaportedafolia">
          <ListItemIcon>
            <NearMeIcon />
          </ListItemIcon>
          <ListItemText primary="Passaporte da Folia" />
        </ListItem>
      </List>
      <Divider />
      <List>
        <ListItem button component={RouterLink} to="/centraldeajuda">
          <ListItemIcon>
            <HelpOutlineOutlinedIcon />
          </ListItemIcon>
          <ListItemText primary="Central de Ajuda" />
        </ListItem>
        <ListItem buttoncomponent={RouterLink} to="/centraldeatendimento">
          <ListItemIcon>
            <HeadsetIcon />
          </ListItemIcon>
          <ListItemText primary="Central de Atendimento" />
        </ListItem>
        <ListItem buttoncomponent={RouterLink} to="/circuitos">
          <ListItemIcon>
            <NearMeIcon />
          </ListItemIcon>
          <ListItemText primary="Conheça os circuitos" />
        </ListItem>
      </List>
      <Divider />
      <List>
        <ListItem buttoncomponent={RouterLink} to="/whatsapp">
          <ListItemIcon>
            <WhatsAppIcon />
          </ListItemIcon>
          <ListItemText primary="Whatsapp" />
        </ListItem>
        <ListItem button component={RouterLink} to="/chat">
          <ListItemIcon>
            <QuestionAnswerOutlinedIcon />
          </ListItemIcon>
          <ListItemText primary="Iniciar o Chat" />
        </ListItem>
      </List>
    </div>
  );
};
