import { CART_UPDATE, CART_ERASE } from "../actions/actionTypes";

const INITIAL_STATE = {
  items: [], //{ produto: {}, quantidade: 0 }
  total: 0
};

function calculaTotal(items) {
  let total = 0;
  items.forEach(item => {
    total += parseFloat(item.produto.price) * item.quantidade;
  });
  return total;
}

function existeItem(items, novoItem) {
  let result = false;
  items.forEach(item => {
    if (item.produto) {
      if (item.produto.id === novoItem.produto.id) {
        result = item;
      }
    }
  });
  return result;
}

export default function cartReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case CART_UPDATE:
      const novoItem = {
        produto: action.produto,
        quantidade: action.quantidade
      };
      let updateItems = state.items;
      let itemExistente = existeItem(updateItems, novoItem);
      if (novoItem.quantidade === 0) {
        updateItems = state.items.filter(
          x => x.produto.id !== action.produto.id
        );
      } else {
        if (itemExistente) {
          itemExistente.quantidade = novoItem.quantidade;
        } else {
          updateItems.push(novoItem);
        }
      }

      return {
        ...state,
        items: updateItems,
        total: calculaTotal(state.items)
      };

    case CART_ERASE: //remove item que ja esta na lista
      return {
        ...state,
        items: [],
        total: 0
      };
    default:
      return state;
  }
}

