import { WOO_API } from "../woocommerce";
import {
  PRODUTOS_GROUPED_GET,
  PRODUTOS_SIMPLE_GET,
  PRODUTOS_ERROR
} from "./actionTypes";

export function produtosGroupedGetAction(products) {
  return { type: PRODUTOS_GROUPED_GET, products };
}

export function produtosSimpleGetAction(products) {
  return { type: PRODUTOS_SIMPLE_GET, products };
}

export function produtosErrorAction(error) {
  return { type: PRODUTOS_ERROR, error };
}

export function produtosGroupedGet(dispatch) {
  WOO_API.get("products/", {
    status: "publish",
    type: "grouped",
    orderby: "menu_order",
    order: "asc",
    per_page: "100"
  })
    .then(response => {
      dispatch(produtosGroupedGetAction(response.data));
    })
    .catch(error => {
      dispatch(produtosErrorAction(error.response));
    });
}

export function produtosSimpleGet(dispatch) {
  WOO_API.get("products/", {
    status: "publish",
    type: "simple",
    orderby: "menu_order",
    order: "asc",
    //meta_key: "lancado_checkbox",
    //meta_value: "yes",
    stock_status: "instock",
    per_page: "100"
  })
    .then(response => {
      dispatch(produtosSimpleGetAction(response.data));
    })
    .catch(error => {
      dispatch(produtosErrorAction(error.response));
    });
}
