import { ORDER_STORE, ORDER_GET, ORDER_ERROR } from "../actions/actionTypes";

const INITIAL_STATE = {
  data: {},
  error: false
};

export default function orderReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case ORDER_STORE:
      return {
        ...state,
        data: action.data,
        error: false
      };
    case ORDER_GET:
      return {
        ...state,
        data: action.data,
        error: false
      };
    case ORDER_ERROR:
      return {
        ...state,
        error: action.error
      };
    default:
      return state;
  }
}
