import {
  CUSTOMER_LOGIN,
  CUSTOMER_LOGOUT,
  CUSTOMER_STORE,
  CUSTOMER_ERROR
} from "./actionTypes";
import { WOO_API } from "../woocommerce";

export function customerLoginAction(dados) {
  return { type: CUSTOMER_LOGIN, dados };
}

export function customerLogoutAction() {
  return { type: CUSTOMER_LOGOUT };
}

export function customerRegisterAction(dados) {
  return { type: CUSTOMER_STORE, dados };
}

export function customerErrorAction(error) {
  return { type: CUSTOMER_ERROR, error };
}

export function customerLogin(dispatch, user, psw) {
  WOO_API.get("customers/26")
    .then(response => {
      dispatch(customerLoginAction(response.data));
    })
    .catch(error => {
      dispatch(customerErrorAction(error.response.data));
    });
}

export function customerLogout(dispatch) {
  dispatch(customerLogoutAction());
}

export function customerRegister(dispatch, dados) {
  dispatch(customerRegisterAction(dados));
}
